.about {
  @extend .section-inner-offset;
  @extend .section-transition;

  &__container {
    @extend .flex-column;
    align-items: center;
    max-width: 1440px;
  }

  &__text {
    margin-bottom: 40px;

    p {
      font-weight: 400;
      font-size: 18px;

      text-align: center;
      margin-bottom: 15px;
      color: var(--brown);
    }
  }
}
